import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import {
  Box,
  Grid,
  Card,
  CardContent,
  Typography,
  Button,
  Container
} from '@mui/material';
import {
  Upload as UploadIcon,
  QrCode as BarcodeIcon,
  Assessment as ReportIcon
} from '@mui/icons-material';

const HomePage: React.FC = () => {
  const navigate = useNavigate();
  const { isAuthenticated, user } = useAuth();

  const menuItems = [
    {
      title: 'Excel Import',
      description: 'Excel dosyasndan rn ykle',
      icon: <UploadIcon sx={{ fontSize: 40 }} />,
      color: '#4caf50',
      onClick: () => navigate('/excel-import')
    },
    {
      title: 'Barkod Oluturucu',
      description: 'rnler iin barkod olutur ve yazdr',
      icon: <BarcodeIcon sx={{ fontSize: 40 }} />,
      color: '#2196f3',
      onClick: () => navigate('/barcode-generator')
    },
    {
      title: 'Gnlk Rapor',
      description: 'Gnlk yklenen sipariler ve istatistikler',
      icon: <ReportIcon sx={{ fontSize: 40 }} />,
      color: '#ff9800',
      onClick: () => navigate('/report')
    }
  ];

  return (
    <Container maxWidth="lg">
      <Box sx={{ mt: 4, mb: 4 }}>
        <Typography variant="h3" component="h1" gutterBottom align="center">
          Barkod Ynetim Sistemi
        </Typography>
        <Typography variant="h6" component="p" color="text.secondary" align="center" sx={{ mb: 4 }}>
          rnlerinizi kolayca ynetin, barkod oluturun ve tarayn
        </Typography>
        
        {!isAuthenticated && (
          <Box sx={{ textAlign: 'center', mb: 4 }}>
            <Typography variant="h6" color="warning.main" gutterBottom>
              Sistemi kullanmak iin giri yapn
            </Typography>
            <Box sx={{ display: 'flex', gap: 2, justifyContent: 'center' }}>
              <Button
                variant="contained"
                onClick={() => navigate('/login')}
                size="large"
              >
                Giri Yap
              </Button>
              <Button
                variant="outlined"
                onClick={() => navigate('/register')}
                size="large"
              >
                Kayt Ol
              </Button>
            </Box>
          </Box>
        )}
        
        {isAuthenticated && user && (
          <Box sx={{ textAlign: 'center', mb: 4 }}>
            <Typography variant="h6" color="success.main" gutterBottom>
              Ho geldin, {user.firstName} {user.lastName}!
            </Typography>
            <Typography variant="body1" color="text.secondary">
              Rol: {user.role === 'ADMIN' ? 'Ynetici' : user.role === 'MANAGER' ? 'Mdr' : 'Kullanc'}
            </Typography>
          </Box>
        )}
        
        {isAuthenticated && (
          <Grid container spacing={3}>
            {menuItems.map((item, index) => (
              <Grid item xs={12} sm={6} md={4} lg={3} key={index}>
                <Card 
                  sx={{ 
                    height: '100%', 
                    display: 'flex', 
                    flexDirection: 'column',
                    cursor: 'pointer',
                    transition: 'transform 0.2s',
                    '&:hover': {
                      transform: 'scale(1.05)',
                      boxShadow: 6
                    }
                  }}
                  onClick={item.onClick}
                >
                  <CardContent sx={{ flexGrow: 1, textAlign: 'center', p: 3 }}>
                    <Box sx={{ color: item.color, mb: 2 }}>
                      {item.icon}
                    </Box>
                    <Typography variant="h6" component="h2" gutterBottom>
                      {item.title}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {item.description}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        )}
      </Box>
    </Container>
  );
};

export default HomePage;
