import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Paper,
  Typography,
  Button,
  Grid,
  Card,
  CardContent,
  Alert,
  CircularProgress,
  Container,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Tabs,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Chip,
  Badge,
  List,
  ListItem,
  ListItemText,
  Divider,
  Snackbar
} from '@mui/material';
import { Download as DownloadIcon, Print as PrintIcon, Group as GroupIcon, Person as PersonIcon, Inventory as ProductIcon, Refresh as RefreshIcon, ArrowBack as BackIcon } from '@mui/icons-material';
import { productApi, barcodeApi, orderApi } from '../services/api';
import { Product, Order } from '../types';

interface Group {
  groupType: string;
  groupName: string;
  items: Order[];
  count: number;
  totalAmount: number;
  barcode?: string; // Excel'den gelen barkod (virglle ayrlm)
  stockCode?: string; // Stok kodu (virglle ayrlm)
}

const BarcodeGeneratorPage: React.FC = () => {
  const navigate = useNavigate();
  const [groups, setGroups] = useState<Group[]>([]);
  const [selectedGroup, setSelectedGroup] = useState<Group | null>(null);
  const [barcodeType, setBarcodeType] = useState('CODE128');
  const [barcodeSize, setBarcodeSize] = useState(300);
  const [loading, setLoading] = useState(false);
  const [updatingStatus, setUpdatingStatus] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState(0);
  const [successMessage, setSuccessMessage] = useState<string | null>(null);
  const [statusFilter, setStatusFilter] = useState<'PENDING' | 'SHIPPED'>('PENDING');

  useEffect(() => {
    loadGroups();
  }, []);

  useEffect(() => {
    loadGroups();
  }, [statusFilter]);

  const loadGroups = async () => {
    try {
      setLoading(true);
      setError(null);
      
      // Siparileri ykle
      const response = await orderApi.getAllOrders();
      console.log('API Response:', response);
      console.log('Response type:', typeof response, 'isArray:', Array.isArray(response));
      
      // Response'un array olup olmadn kontrol et
      const allOrders: Order[] = Array.isArray(response) ? response : [];
      
      let filteredOrders: Order[];
      
      if (statusFilter === 'SHIPPED') {
        // Kargoya Verildi: Sadece bugn oluturulan SHIPPED siparileri gster
        const today = new Date();
        
        // Yerel saat diliminde bugnn tarihini al
        const year = today.getFullYear();
        const month = String(today.getMonth() + 1).padStart(2, '0');
        const day = String(today.getDate()).padStart(2, '0');
        const todayStr = `${year}-${month}-${day}`;
        
        console.log('====== FLTRELEME BALIYOR ======');
        console.log('Bugnn tarihi (yerel saat):', todayStr);
        console.log('Bugnn tam tarihi:', today.toString());
        console.log('Toplam SHIPPED sipari says:', allOrders.filter(o => o.status === 'SHIPPED').length);
        
        filteredOrders = allOrders.filter(order => {
          if (order.status !== 'SHIPPED') return false;
          
          // createdAt tarihi bugn m kontrol et
          if (!order.createdAt) {
            console.log(` Sipari ${order.orderNumber}: createdAt yok`);
            return false;
          }
          
          // createdAt'ten sadece tarih ksmn al (timezone bamsz)
          let orderDateStr: string;
          if (order.createdAt.includes('T')) {
            orderDateStr = order.createdAt.split('T')[0];
          } else if (order.createdAt.includes(' ')) {
            orderDateStr = order.createdAt.split(' ')[0];
          } else {
            orderDateStr = order.createdAt.substring(0, 10);
          }
          
          const isToday = orderDateStr === todayStr;
          
          if (isToday) {
            console.log(` Sipari ${order.orderNumber}: createdAt=${order.createdAt}, tarih=${orderDateStr} (BUGN)`);
          } else {
            console.log(` Sipari ${order.orderNumber}: createdAt=${order.createdAt}, tarih=${orderDateStr} (Farkl gn - bugn: ${todayStr})`);
          }
          
          return isToday;
        });
        
        console.log('====== FLTRELEME BTT ======');
        console.log('Bugn oluturulan SHIPPED sipariler:', filteredOrders.length);
      } else {
        // Beklemede: Tm PENDING sipariler
        filteredOrders = allOrders.filter(order => order.status === 'PENDING');
        console.log('PENDING Sipariler:', filteredOrders.length);
      }
      
      console.log('Tm Sipariler:', allOrders.length);
      console.log(`${statusFilter} Sipariler:`, filteredOrders.length);
      console.log('First order:', filteredOrders[0]);
      console.log('First order items:', filteredOrders[0]?.orderItems);
      
      // Hibrit gruplandrma
      const groupedData = createHybridGroups(filteredOrders);
      setGroups(groupedData);
      
    } catch (err: any) {
      console.error('Load groups error:', err);
      setError('Gruplar yklenirken hata olutu: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const createHybridGroups = (orders: Order[]): Group[] => {
    const groups: Group[] = [];
    
    // Alc + Stok Kodu baznda gruplandrma yap
    const aliciStokGroups = new Map<string, Order[]>();
    
    orders.forEach(order => {
      // Alc adn doru ekilde al
      let aliciName = order.customerName;
      
      // Eer customerName tarih gibi grnyorsa, orderNumber'dan alc adn karmaya al
      if (aliciName && (aliciName.includes('/') || aliciName.includes('-') || /^\d+$/.test(aliciName))) {
        console.log('CustomerName tarih gibi grnyor:', aliciName, 'OrderNumber:', order.orderNumber);
        // orderNumber formatndan alc adn karmaya al
        const orderParts = order.orderNumber.split('_');
        if (orderParts.length > 1) {
          aliciName = orderParts[1]; // kinci ksm alc ad olabilir
          console.log('OrderNumber\'dan alc ad karld:', aliciName);
        } else {
          aliciName = order.orderNumber; // Fallback olarak orderNumber kullan
          console.log('Fallback olarak orderNumber kullanld:', aliciName);
        }
      }
      
      // Stok kodu iin nce Order'dan al, yoksa OrderItem'dan al
      let stockCode = order.stockCode || '';
      
      // Eer Order'da stok kodu yoksa OrderItem'lardan al
      if (!stockCode && order.orderItems && order.orderItems.length > 0) {
        const stockCodes = order.orderItems
          .map(item => item.stockCode || item.product?.barcode || item.product?.name || '')
          .filter(code => code && code.trim() !== '');
        stockCode = stockCodes.length > 0 ? stockCodes[0] : '';
      }
      
      // Eer hala stok kodu yoksa orderNumber'dan karmaya al
      if (!stockCode) {
        stockCode = order.orderNumber.split('-')[0] || order.orderNumber;
      }
      
      console.log('Order:', order.orderNumber);
      console.log('OrderItems:', order.orderItems?.length || 0);
      console.log('Order.stockCode:', order.stockCode);
      console.log('Final aliciName:', aliciName, 'StockCode:', stockCode);
      
      const groupKey = `${aliciName}_${stockCode}`; // zlem Karata_ABC123
      
      if (!aliciStokGroups.has(groupKey)) {
        aliciStokGroups.set(groupKey, []);
      }
      aliciStokGroups.get(groupKey)!.push(order);
    });
    
    // Alc+Stok Kodu gruplarn ekle
    aliciStokGroups.forEach((orders, groupKey) => {
      const [aliciName] = groupKey.split('_'); // lk ksm alc ad
      const totalQuantity = orders.reduce((sum, order) => sum + order.totalItems, 0);
      
      // lk sipariten sipari numarasn al
      const orderNumberForGroup = orders[0]?.orderNumber || 'Bilinmeyen';
      
      let groupType = '1li';
      if (totalQuantity === 2) groupType = '2li';
      else if (totalQuantity === 3) groupType = '3lu';
      else if (totalQuantity === 4) groupType = '4lu';
      else if (totalQuantity >= 5) groupType = '5veUstu';
      
      // Tm benzersiz stok kodlarn ve barkodlar topla
      const allStockCodes = new Set<string>();
      const allBarcodes = new Set<string>();
      
      orders.forEach(order => {
        // Order'dan barkod al
        if (order.barcode && order.barcode.trim()) {
          allBarcodes.add(order.barcode);
        }
        // Order'dan stok kodu al
        if (order.stockCode) {
          allStockCodes.add(order.stockCode);
        }
        // OrderItem'lardan stok kodlarn al
        if (order.orderItems) {
          order.orderItems.forEach(item => {
            const code = item.stockCode || item.product?.name;
            if (code && code.trim()) {
              allStockCodes.add(code);
            }
          });
        }
      });
      
      // Virglle ayrlm stok kodlar ve barkodlar
      const stockCodesList = Array.from(allStockCodes).join(', ');
      const barcodesList = Array.from(allBarcodes).join(', ');
      const firstBarcode = Array.from(allBarcodes)[0] || '';
      const firstStockCode = Array.from(allStockCodes)[0] || '';
      
      const groupName = `${groupType.toUpperCase()} - ${aliciName} - ${orderNumberForGroup}`;
      
      console.log('Grup oluturuluyor:', {
        groupKey,
        aliciName,
        barcodesList,
        firstBarcode,
        stockCodesList,
        firstStockCode,
        orderNumberForGroup,
        totalQuantity
      });
      
      groups.push({
        groupType,
        groupName,
        items: orders,
        count: orders.length,
        totalAmount: orders.reduce((sum, order) => sum + order.totalAmount, 0),
        barcode: barcodesList, // Tm barkodlar virglle ayrarak kaydet
        stockCode: stockCodesList // Tm stok kodlarn virglle ayrarak kaydet
      });
    });
    
    return groups.sort((a, b) => {
      const order = ['1li', '2li', '3lu', '4lu', '5veUstu', 'diger'];
      return order.indexOf(a.groupType) - order.indexOf(b.groupType);
    });
  };

  const handleGenerateBarcode = () => {
    if (!selectedGroup) {
      setError('Ltfen bir grup sein!');
      return;
    }
    
    console.log('Selected Group:', selectedGroup);
    
    // Grup tipine gre barkod olutur
    let barcodeData = '';
    let groupTitle = '';
    
    // Excel'den gelen barcode alann kullan (ilk barkod)
    const firstBarcode = selectedGroup.barcode?.split(',')[0].trim() || '';
    const firstStockCode = selectedGroup.stockCode?.split(',')[0].trim() || '';
    
    if (selectedGroup.groupType === '1li') {
      // 1'li grup: Excel'den gelen barkod
      barcodeData = firstBarcode || firstStockCode || selectedGroup.groupName;
      groupTitle = `1'li Grup - ${firstStockCode || 'Stok Kodu Yok'}`;
    } else {
      // 2+ li grup: lk barkod (barkod iin), tm stok kodlar (gsterim iin)
      barcodeData = firstBarcode || firstStockCode || selectedGroup.groupName;
      groupTitle = `${selectedGroup.groupType.toUpperCase()} Grup - ${selectedGroup.stockCode || 'Stok Kodu Yok'}`;
    }
    
    console.log('Barcode Data:', barcodeData);
    console.log('Barcode Type:', barcodeType);
    console.log('Barcode Size:', barcodeSize);
    
    if (!barcodeData) {
      setError('Barkod verisi bulunamad!');
      return;
    }
    
    const barcodeUrl = barcodeApi.getBarcodeImage(
      barcodeData,
      barcodeType,
      barcodeSize,
      barcodeSize
    );
    
    console.log('Barcode URL:', barcodeUrl);
    
    // Barkod resmini yeni pencerede a
    const newWindow = window.open('', '_blank');
    if (newWindow) {
      newWindow.document.write(`
        <html>
          <head>
            <title>Barkod - ${groupTitle}</title>
            <style>
              body { 
                display: flex; 
                justify-content: center; 
                align-items: center; 
                height: 100vh; 
                margin: 0; 
                font-family: Arial, sans-serif;
              }
              .barcode-container {
                text-align: center;
                padding: 20px;
                border: 2px solid #ccc;
                border-radius: 10px;
                background: white;
              }
              .barcode-info {
                margin-top: 20px;
                font-size: 14px;
                color: #666;
              }
            </style>
          </head>
          <body>
            <div class="barcode-container">
              <img src="${barcodeUrl}" alt="Barkod" style="max-width: 100%; height: auto;" onerror="alert('Barkod yklenemedi: ' + this.src)" />
              <div class="barcode-info">
                <div style="margin-top: 15px;">
                  <p><strong>Sipari Detaylar:</strong></p>
                  ${selectedGroup.items.map(order => `
                    <div style="margin: 10px 0; padding: 8px; background: #f5f5f5; border-radius: 3px;">
                      <strong>Sipari No:</strong> ${order.orderNumber}<br>
                      <strong>Alc:</strong> ${order.customerName}<br>
                      <strong>Teslimat Adresi:</strong> ${order.deliveryAddress || order.address || 'Yok'}<br>
                      ${order.brand ? `<strong>Marka:</strong> ${order.brand}<br>` : ''}
                      ${order.orderItems && order.orderItems.length > 0 ? `
                        <div style="margin-top: 8px; padding-left: 10px; border-left: 3px solid #2196F3;">
                          ${order.orderItems.map((item, index) => `
                            <div style="margin: 5px 0; padding: 5px; background: white; border-radius: 3px;">
                              <strong>${index + 1}. rn:</strong><br>
                              <strong>Stok Kodu:</strong> ${item.stockCode || item.product?.barcode || 'Yok'}<br>
                              <strong>rn Ad:</strong> ${item.product?.name || 'Yok'}<br>
                              <strong>Adet:</strong> ${item.quantity}
                            </div>
                        `).join('')}
                        </div>
                      ` : ''}
                    </div>
                  `).join('')}
                </div>
              </div>
            </div>
          </body>
        </html>
      `);
      newWindow.document.close();
    }
  };

  const handlePrintAllGroups = (groupsToPrint: Group[]) => {
    if (!groupsToPrint || groupsToPrint.length === 0) {
      setError('Yazdrlacak grup yok!');
      return;
    }
    
    // Yazdrma iin yeni pencere
    const printWindow = window.open('', '_blank');
    if (printWindow) {
      // Tm gruplarn tm siparilerini topla
      const allPages: string[] = [];
      
      let pageIndex = 0;
      let totalPages = 0;
      
      // nce toplam sayfa saysn hesapla
      groupsToPrint.forEach(group => {
        totalPages += group.items.length;
      });
      
      groupsToPrint.forEach(group => {
        // Her grup iin her siparii ayr sayfa olarak ekle
        group.items.forEach(order => {
          pageIndex++;
          const isLastPage = pageIndex === totalPages;
          
          // Her sipariin kendi kargo kodu ile barkod olutur
          const orderBarcode = order.barcode || order.stockCode || '';
          
          const barcodeUrl = barcodeApi.getBarcodeImage(
            orderBarcode,
            barcodeType,
            barcodeSize,
            barcodeSize
          );
          
          // Son sayfa iin farkl class kullan
          const pageClass = isLastPage ? 'page last-page' : 'page';
          
          allPages.push(`
            <div class="${pageClass}">
              <div class="group-info">
                <h4>Sipari Detaylar:</h4>
                <div style="margin: 10px 0; padding: 8px; background: #f5f5f5; border-radius: 3px;">
                  <strong>Sipari No:</strong> ${order.orderNumber}<br>
                  <strong>Alc:</strong> ${order.customerName}<br>
                  <strong>Teslimat Adresi:</strong> ${order.deliveryAddress || order.address || 'Yok'}<br>
                  ${order.brand ? `<strong>Marka:</strong> ${order.brand}<br>` : ''}
                  ${order.orderItems && order.orderItems.length > 0 ? `
                    <div style="margin-top: 8px; padding-left: 10px; border-left: 3px solid #2196F3;">
                      ${order.orderItems.map((item, index) => `
                        <div style="margin: 5px 0; padding: 5px; background: white; border-radius: 3px;">
                          <strong>${index + 1}. rn:</strong><br>
                          ${item.product?.barcode ? `<strong>Barkod:</strong> ${item.product.barcode}<br>` : ''}
                          <strong>Stok Kodu:</strong> ${item.stockCode || item.product?.stockCode || 'Yok'}<br>
                          <strong>rn Ad:</strong> ${item.product?.name || 'Yok'}<br>
                          <strong>Adet:</strong> ${item.quantity}
                        </div>
                      `).join('')}
                    </div>
                  ` : ''}
                </div>
              </div>
              <div class="barcode" style="margin-top: 30px;">
                <h3 style="margin-bottom: 10px; font-size: 18px;">Kargo Kampanya Kodu:</h3>
                <p style="font-size: 20px; font-weight: bold; margin: 10px 0;">${orderBarcode}</p>
                <img src="${barcodeUrl}" alt="Barkod" style="max-width: 400px; margin-top: 10px;" />
              </div>
            </div>
          `);
        });
      });
      
      printWindow.document.write(`
        <html>
          <head>
            <title>Tm Gruplar - ${groupsToPrint.length} Grup, ${allPages.length} Sipari</title>
            <style>
              body { 
                text-align: center; 
                font-family: Arial, sans-serif;
                margin: 0;
                padding: 20px;
              }
              .page {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding: 40px 20px;
              }
              .page:not(:last-child) {
                min-height: 100vh;
                page-break-after: always;
              }
              .page:last-child {
                min-height: auto;
                page-break-after: auto;
              }
              .barcode { 
                margin: 30px 0; 
              }
              .group-info { 
                margin: 10px 0;
                max-width: 800px;
                width: 100%;
              }
              @media print {
                .page:not(:last-child) {
                  min-height: 100vh;
                  page-break-after: always !important;
                  break-after: page !important;
                }
                .page:last-child {
                  min-height: auto !important;
                  height: auto !important;
                  page-break-after: auto !important;
                  break-after: auto !important;
                }
              }
            </style>
          </head>
          <body>
            ${allPages.join('')}
            <script>
              window.onload = function() {
                // Son sayfann page-break'ini kaldr
                const pages = document.querySelectorAll('.page');
                if (pages.length > 0) {
                  const lastPage = pages[pages.length - 1];
                  lastPage.style.pageBreakAfter = 'auto';
                  lastPage.style.breakAfter = 'auto';
                }
                window.print();
              }
            </script>
          </body>
        </html>
      `);
      printWindow.document.close();
      
      // Yazdrma tamamlandnda otomatik SHIPPED yap
      printWindow.onafterprint = async () => {
        console.log(' Yazdrma tamamland, durumlar gncelleniyor...');
        
        try {
          const updatePromises = groupsToPrint.flatMap(group => 
            group.items.map(order => 
              orderApi.updateOrderStatus(order.id, 'SHIPPED')
                .then(() => console.log(` Sipari ${order.orderNumber} durumu gncellendi: SHIPPED`))
                .catch(err => console.error(` Sipari ${order.orderNumber} durumu gncellenemedi:`, err))
            )
          );
          
          await Promise.all(updatePromises);
          
          console.log(' Tm sipariler SHIPPED olarak iaretlendi');
          setSuccessMessage(' Barkodlar yazdrld ve sipariler "Kargoya Verildi" olarak iaretlendi!');
          
          // Gruplar yeniden ykle
          await loadGroups();
        } catch (err) {
          console.error(' Durum gncellenirken hata:', err);
          setError('Durum gncellenirken hata olutu');
        }
      };
      
      console.log(' Yazdrma penceresi ald.');
    }
  };

  const handleMarkAsShipped = async (groupsToShip: Group[]) => {
    if (!groupsToShip || groupsToShip.length === 0) {
      setError('lenecek grup yok!');
      return;
    }
    
    try {
      setUpdatingStatus(true);
      console.log(' Sipariler manuel olarak SHIPPED yaplyor...');
      
      const updatePromises = groupsToShip.flatMap(group => 
        group.items.map(order => 
          orderApi.updateOrderStatus(order.id, 'SHIPPED')
            .then(() => console.log(` Sipari ${order.orderNumber} manuel olarak SHIPPED yapld`))
            .catch(err => console.error(` Sipari ${order.orderNumber} durumu gncellenemedi:`, err))
        )
      );
      
      await Promise.all(updatePromises);
      
      console.log(' Tm sipariler manuel olarak SHIPPED olarak iaretlendi');
      setSuccessMessage(` ${groupsToShip.length} grup iindeki tm sipariler "Kargoya Verildi" olarak iaretlendi!`);
      
      // Gruplar yeniden ykle
      await loadGroups();
      
    } catch (err: any) {
      console.error(' Durum gncellenirken hata:', err);
      setError('Durum gncellenirken hata olutu: ' + err.message);
    } finally {
      setUpdatingStatus(false);
    }
  };

  const handleMarkAsPending = async (groupsToRevert: Group[]) => {
    if (!groupsToRevert || groupsToRevert.length === 0) {
      setError('lenecek grup yok!');
      return;
    }
    
    try {
      setUpdatingStatus(true);
      console.log(' Sipariler PENDING yaplyor...');
      
      const updatePromises = groupsToRevert.flatMap(group => 
        group.items.map(order => 
          orderApi.updateOrderStatus(order.id, 'PENDING')
            .then(() => console.log(` Sipari ${order.orderNumber} PENDING yapld`))
            .catch(err => console.error(` Sipari ${order.orderNumber} durumu gncellenemedi:`, err))
        )
      );
      
      await Promise.all(updatePromises);
      
      console.log(' Tm sipariler PENDING olarak iaretlendi');
      setSuccessMessage(` ${groupsToRevert.length} grup iindeki tm sipariler geri alnd ve "Beklemede" durumuna getirildi!`);
      
      // Gruplar yeniden ykle
      await loadGroups();
      
    } catch (err: any) {
      console.error(' Durum gncellenirken hata:', err);
      setError('Durum gncellenirken hata olutu: ' + err.message);
    } finally {
      setUpdatingStatus(false);
    }
  };

  const handlePrintBarcode = () => {
    if (!selectedGroup) return;
    
    // Excel'den gelen barcode alann kullan (ilk barkod)
    const firstBarcode = selectedGroup.barcode?.split(',')[0].trim() || '';
    const firstStockCode = selectedGroup.stockCode?.split(',')[0].trim() || '';
    
    // Barkod iin kargo kodunu (order.barcode) veya stok kodunu kullan
    const barcodeData = firstBarcode || firstStockCode || selectedGroup.groupName;
    
    const barcodeUrl = barcodeApi.getBarcodeImage(
      barcodeData,
      barcodeType,
      barcodeSize,
      barcodeSize
    );
    
    // Yazdrma iin yeni pencere - her sipari iin ayr sayfa
    const printWindow = window.open('', '_blank');
    if (printWindow) {
      // Her sipari iin ayr sayfa olutur
      const pages = selectedGroup.items.map((order, orderIndex) => `
        <div class="page" style="page-break-after: always;">
          <div class="group-info">
            <h4>Sipari Detaylar:</h4>
            <div style="margin: 10px 0; padding: 8px; background: #f5f5f5; border-radius: 3px;">
              <strong>Sipari No:</strong> ${order.orderNumber}<br>
              <strong>Alc:</strong> ${order.customerName}<br>
              <strong>Teslimat Adresi:</strong> ${order.deliveryAddress || order.address || 'Yok'}<br>
              ${order.brand ? `<strong>Marka:</strong> ${order.brand}<br>` : ''}
              ${order.orderItems && order.orderItems.length > 0 ? `
                <div style="margin-top: 8px; padding-left: 10px; border-left: 3px solid #2196F3;">
                  ${order.orderItems.map((item, index) => `
                    <div style="margin: 5px 0; padding: 5px; background: white; border-radius: 3px;">
                      <strong>${index + 1}. rn:</strong><br>
                      <strong>Stok Kodu:</strong> ${item.stockCode || item.product?.barcode || 'Yok'}<br>
                      <strong>rn Ad:</strong> ${item.product?.name || 'Yok'}<br>
                      <strong>Adet:</strong> ${item.quantity}
                    </div>
                  `).join('')}
                </div>
              ` : ''}
            </div>
          </div>
          <div class="barcode">
            <img src="${barcodeUrl}" alt="Barkod" style="max-width: 400px;" />
          </div>
        </div>
      `).join('');
      
      printWindow.document.write(`
        <html>
          <head>
            <title>Barkod Yazdr - ${selectedGroup.items.length} Sipari</title>
            <style>
              body { 
                text-align: center; 
                font-family: Arial, sans-serif;
                margin: 0;
                padding: 20px;
              }
              .page {
                min-height: 100vh;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
              }
              .barcode { 
                margin: 30px 0; 
              }
              .group-info { 
                margin: 10px 0;
                max-width: 800px;
                width: 100%;
              }
              @media print {
                .page {
                  page-break-after: always;
                }
              }
            </style>
          </head>
          <body>
            ${pages}
            <script>
              window.onload = function() {
                window.print();
              }
            </script>
          </body>
        </html>
      `);
      printWindow.document.close();
    }
  };

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setActiveTab(newValue);
  };

  const getGroupedByType = () => {
    const grouped = {
      '1li': groups.filter(g => g.groupType === '1li'),
      '2li': groups.filter(g => g.groupType === '2li'),
      '3lu': groups.filter(g => g.groupType === '3lu'),
      '4lu': groups.filter(g => g.groupType === '4lu'),
      '5veUstu': groups.filter(g => g.groupType === '5veUstu')
    };
    return grouped;
  };

  const renderGroupList = (groupList: Group[], groupType: string) => (
    <List>
      {groupList.map((group, index) => (
        <React.Fragment key={index}>
          <ListItem 
            button 
            onClick={() => setSelectedGroup(group)}
            selected={selectedGroup?.groupName === group.groupName}
            sx={{ 
              border: selectedGroup?.groupName === group.groupName ? '2px solid #1976d2' : '1px solid #e0e0e0',
              borderRadius: 1,
              mb: 1
            }}
          >
            <ListItemText
              primary={
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                  <Chip 
                    label={group.groupType.toUpperCase()} 
                    color={groupType === '1li' ? 'primary' : 'secondary'} 
                    size="small" 
                  />
                  <Typography variant="subtitle1" sx={{ fontWeight: 'bold' }}>
                    {group.groupName.split(' - ')[1]}
                  </Typography>
                </Box>
              }
              secondary={
                <Box>
                  <Typography variant="body2" color="text.secondary">
                    Sipari Says: {group.count} 
                  </Typography>
                  {groupType === '1li' && (
                    <Typography variant="caption" color="info.main">
                      Stok kodu baznda gruplandrlm
                    </Typography>
                  )}
                  {groupType !== '1li' && (
                    <Typography variant="caption" color="info.main">
                      Mteri baznda gruplandrlm
                    </Typography>
                  )}
                </Box>
              }
            />
          </ListItem>
          {index < groupList.length - 1 && <Divider />}
        </React.Fragment>
      ))}
    </List>
  );

  const groupedByType = getGroupedByType();

  return (
    <Container maxWidth="lg">
      <Box sx={{ mt: 4, mb: 4 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
            <Button
              startIcon={<BackIcon />}
              onClick={() => navigate('/')}
            >
              Ana Sayfa
            </Button>
            <Typography variant="h4" component="h1">
          Grup Bazl Barkod Oluturucu
        </Typography>
          </Box>
          <Button
            variant="outlined"
            onClick={loadGroups}
            disabled={loading}
            startIcon={loading ? <CircularProgress size={20} /> : <RefreshIcon />}
          >
            {loading ? 'Ykleniyor...' : 'Gruplar Ykle'}
          </Button>
        </Box>

        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}

        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Paper elevation={3} sx={{ p: 3 }}>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Typography variant="h6">
                  Sipari Gruplar
                </Typography>
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <Button
                    variant={statusFilter === 'PENDING' ? 'contained' : 'outlined'}
                    color="warning"
                    size="small"
                    onClick={() => setStatusFilter('PENDING')}
                  >
                    Beklemede
                  </Button>
                  <Button
                    variant={statusFilter === 'SHIPPED' ? 'contained' : 'outlined'}
                    color="success"
                    size="small"
                    onClick={() => setStatusFilter('SHIPPED')}
                  >
                    Kargoya Verildi
                  </Button>
                </Box>
              </Box>
              
              <Tabs value={activeTab} onChange={handleTabChange} variant="scrollable" scrollButtons="auto">
                <Tab 
                  label={
                    <Badge badgeContent={groupedByType['1li'].length} color="primary">
                      1'li Gruplar
                    </Badge>
                  } 
                />
                <Tab 
                  label={
                    <Badge badgeContent={groupedByType['2li'].length} color="secondary">
                      2'li Gruplar
                    </Badge>
                  } 
                />
                <Tab 
                  label={
                    <Badge badgeContent={groupedByType['3lu'].length} color="error">
                      3'l Gruplar
                    </Badge>
                  } 
                />
                <Tab 
                  label={
                    <Badge badgeContent={groupedByType['4lu'].length} color="warning">
                      4'l Gruplar
                    </Badge>
                  } 
                />
                <Tab 
                  label={
                    <Badge badgeContent={groupedByType['5veUstu'].length} color="success">
                      5+ Gruplar
                    </Badge>
                  } 
                />
              </Tabs>

              <Box sx={{ mt: 2, mb: 2, display: 'flex', gap: 2 }}>
                {statusFilter === 'PENDING' && (
                  <Button
                    variant="contained"
                    color="success"
                    fullWidth
                    startIcon={<PrintIcon />}
                    onClick={() => {
                      const currentGroups = 
                        activeTab === 0 ? groupedByType['1li'] :
                        activeTab === 1 ? groupedByType['2li'] :
                        activeTab === 2 ? groupedByType['3lu'] :
                        activeTab === 3 ? groupedByType['4lu'] :
                        groupedByType['5veUstu'];
                      handlePrintAllGroups(currentGroups);
                    }}
                    disabled={
                      (activeTab === 0 && groupedByType['1li'].length === 0) ||
                      (activeTab === 1 && groupedByType['2li'].length === 0) ||
                      (activeTab === 2 && groupedByType['3lu'].length === 0) ||
                      (activeTab === 3 && groupedByType['4lu'].length === 0) ||
                      (activeTab === 4 && groupedByType['5veUstu'].length === 0)
                    }
                  >
                    Tm {
                      activeTab === 0 ? "1'li" :
                      activeTab === 1 ? "2'li" :
                      activeTab === 2 ? "3'l" :
                      activeTab === 3 ? "4'l" :
                      "5+"
                    } Gruplar Yazdr ({
                      activeTab === 0 ? groupedByType['1li'].length :
                      activeTab === 1 ? groupedByType['2li'].length :
                      activeTab === 2 ? groupedByType['3lu'].length :
                      activeTab === 3 ? groupedByType['4lu'].length :
                      groupedByType['5veUstu'].length
                    })
                  </Button>
                )}

                {statusFilter === 'SHIPPED' && (
                  <Button
                    variant="contained"
                    color="warning"
                    fullWidth
                    startIcon={updatingStatus ? <CircularProgress size={20} color="inherit" /> : undefined}
                    onClick={() => {
                      const currentGroups = 
                        activeTab === 0 ? groupedByType['1li'] :
                        activeTab === 1 ? groupedByType['2li'] :
                        activeTab === 2 ? groupedByType['3lu'] :
                        activeTab === 3 ? groupedByType['4lu'] :
                        groupedByType['5veUstu'];
                      handleMarkAsPending(currentGroups);
                    }}
                    disabled={
                      updatingStatus ||
                      (activeTab === 0 && groupedByType['1li'].length === 0) ||
                      (activeTab === 1 && groupedByType['2li'].length === 0) ||
                      (activeTab === 2 && groupedByType['3lu'].length === 0) ||
                      (activeTab === 3 && groupedByType['4lu'].length === 0) ||
                      (activeTab === 4 && groupedByType['5veUstu'].length === 0)
                    }
                  >
                    {updatingStatus ? 'Geri Alnyor...' : `Geri Al (${
                      activeTab === 0 ? groupedByType['1li'].length :
                      activeTab === 1 ? groupedByType['2li'].length :
                      activeTab === 2 ? groupedByType['3lu'].length :
                      activeTab === 3 ? groupedByType['4lu'].length :
                      groupedByType['5veUstu'].length
                    })`}
                  </Button>
                )}
              </Box>

              <Box sx={{ mt: 2, maxHeight: 400, overflow: 'auto' }}>
                {activeTab === 0 && renderGroupList(groupedByType['1li'], '1li')}
                {activeTab === 1 && renderGroupList(groupedByType['2li'], '2li')}
                {activeTab === 2 && renderGroupList(groupedByType['3lu'], '3lu')}
                {activeTab === 3 && renderGroupList(groupedByType['4lu'], '4lu')}
                {activeTab === 4 && renderGroupList(groupedByType['5veUstu'], '5veUstu')}
              </Box>
            </Paper>
          </Grid>
        </Grid>

        {selectedGroup && (
          <Card sx={{ mt: 3 }}>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Seilen Grup
              </Typography>
              <Typography variant="body1">
                <strong>Grup Ad:</strong> {selectedGroup.groupName}
              </Typography>
              <Typography variant="body1">
                <strong>Grup Tipi:</strong> {selectedGroup.groupType.toUpperCase()}
              </Typography>
              <Typography variant="body1">
                <strong>Sipari Says:</strong> {selectedGroup.count}
              </Typography>
              
              
              <Divider sx={{ my: 2 }} />
              
              <Typography variant="subtitle1" gutterBottom>
                Grup indeki Sipariler:
              </Typography>
              <TableContainer>
                <Table size="small">
                  <TableHead>
                    <TableRow>
                      <TableCell>Sipari No</TableCell>
                      <TableCell>Alc</TableCell>
                      <TableCell>rn Says</TableCell>
                      <TableCell>Tutar</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {selectedGroup.items.map((order) => (
                      <TableRow key={order.id}>
                        <TableCell>{order.orderNumber}</TableCell>
                        <TableCell>{order.customerName}</TableCell>
                        <TableCell>{order.totalItems}</TableCell>
                        <TableCell>{order.totalAmount.toFixed(2)}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </CardContent>
          </Card>
        )}

        {/* Success Snackbar */}
        <Snackbar
          open={!!successMessage}
          autoHideDuration={6000}
          onClose={() => setSuccessMessage(null)}
          message={successMessage}
          anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        />
      </Box>
    </Container>
  );
};

export default BarcodeGeneratorPage;
