import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Paper,
  Typography,
  Button,
  Alert,
  CircularProgress,
  Container,
  LinearProgress
} from '@mui/material';
import { ArrowBack as ArrowBackIcon, Upload as UploadIcon } from '@mui/icons-material';
import axios from 'axios';

const ExcelImportPage: React.FC = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);

  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      if (!file.name.toLowerCase().endsWith('.xlsx')) {
        setError('Sadece .xlsx dosyalar desteklenir.');
        return;
      }
      setSelectedFile(file);
      setError(null);
      setSuccess(null);
    }
  };

  const handleImport = async () => {
    if (!selectedFile) {
      setError('Ltfen bir dosya sein.');
      return;
    }

    const formData = new FormData();
    formData.append('file', selectedFile);

    setLoading(true);
    setError(null);
    setSuccess(null);

    try {
      console.log('Excel import balyor...');
      const response = await axios.post('http://localhost:8080/api/simple-excel/import', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });

      console.log('Import yant:', response.data);
      
      if (response.data.success) {
        setSuccess(response.data.message || 'Excel dosyas baaryla yklendi!');
        setSelectedFile(null);
        // Dosya input'unu temizle
        const fileInput = document.getElementById('excel-file-input') as HTMLInputElement;
        if (fileInput) fileInput.value = '';
      } else {
        setError(response.data.error || 'Import ilemi baarsz oldu.');
      }
    } catch (err: any) {
      console.error('Import hatas:', err);
      setError('Excel import srasnda bir hata olutu: ' + (err.response?.data?.error || err.message));
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="md">
      <Box sx={{ py: 4 }}>
        <Paper elevation={3} sx={{ p: 4 }}>
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
            <Button
              startIcon={<ArrowBackIcon />}
              onClick={() => navigate('/')}
              sx={{ mr: 2 }}
            >
              Geri
            </Button>
            <Typography variant="h4" component="h1">
              Excel Dosyas Ykle
            </Typography>
          </Box>

          <Typography variant="body1" color="text.secondary" paragraph>
            Excel dosyanz sein ve ieriini veritabanna ykleyin.
          </Typography>

          {error && (
            <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
              {error}
            </Alert>
          )}

          {success && (
            <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
              {success}
            </Alert>
          )}

          <Box sx={{ mb: 3 }}>
            <input
              accept=".xlsx"
              style={{ display: 'none' }}
              id="excel-file-input"
              type="file"
              onChange={handleFileSelect}
            />
            <label htmlFor="excel-file-input">
              <Button
                variant="outlined"
                component="span"
                fullWidth
                sx={{ py: 2, mb: 2 }}
              >
                {selectedFile ? selectedFile.name : 'Excel Dosyas Se (.xlsx)'}
              </Button>
            </label>

            <Button
              variant="contained"
              onClick={handleImport}
              disabled={!selectedFile || loading}
              fullWidth
              size="large"
              startIcon={loading ? <CircularProgress size={20} /> : <UploadIcon />}
            >
              {loading ? 'Ykleniyor...' : 'Excel\'i Ykle'}
            </Button>
          </Box>

          {loading && <LinearProgress sx={{ mb: 2 }} />}

          <Alert severity="info" sx={{ mt: 3 }}>
            <Typography variant="body2">
              <strong>Not:</strong> Excel dosyanz u stunlar iermelidir:
            </Typography>
            <Typography variant="caption" component="div" sx={{ mt: 1 }}>
               Stun 0: Barkod<br />
               Stun 6: Kargo Kodu<br />
               Stun 7: Sipari Numaras<br />
               Stun 8: Alc<br />
               Stun 9: Teslimat Adresi<br />
               Stun 10: l<br />
               Stun 11: le<br />
               Stun 12: rn Ad<br />
               Stun 16: E-Posta<br />
               Stun 18: Marka<br />
               Stun 19: Stok Kodu
            </Typography>
          </Alert>
        </Paper>
      </Box>
    </Container>
  );
};

export default ExcelImportPage;
